import { Component, OnInit } from '@angular/core';

import { NewService } from '../../angular-services/new.service';
import { New } from '../../classes/new';

@Component({
  selector: 'app-list-news-admin',
  templateUrl: './list-news.component.html',
  styleUrls: ['./list-news.component.css']
})
export class ListNewsComponent implements OnInit {

  news: New[] = [];
  alert: any = { type: '', message: '', show: false };

  constructor(private newService: NewService) { }

  ngOnInit() {
    this.getNews();
  }

  getNews() {
    this.newService.getNews().subscribe(
      response => {
        if (response !== undefined) {
          this.news = response;
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao carregar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  removeNew(news) {
    let confirm = prompt('Para confirmar esta operação digite SIM: \n\n ');
    if (confirm === 'SIM') {
      this.news = this.news.filter(c => c !== news);
      this.newService.removeNew(news._key).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Notícia excluída com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao carregar os dados!';
            this.alert.show = true;
          }
        }
      )
    } else {
      this.alert.type = 'danger';
      this.alert.message = 'O captcha inválido!';
      this.alert.show = true;
    }
  }

}
